import React, {useState, useEffect} from 'react';
import s from "./Paginator.module.css";


const Paginator = ({totalUsersCount, pageSize, currentPage, onPageChanged, portionSize = 10}) => {

    let pagesCount = Math.ceil(totalUsersCount / pageSize);
    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }
    let portionCount = Math.ceil(pagesCount / portionSize);
    let [portionNumber, setPortionNumber] = useState(1);
    let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
    let rightPortionPageNumber = portionNumber * portionSize;


    let goUp =() => {
        let top = Math.max(window.pageYOffset);
        if (top > 0) {

            window.scrollBy(0, -1500);
            setTimeout('goUp()', 6000);
        }
    };


    return (

        <div className={s.paginator}>
            {portionNumber > 1 && <button onClick={() => {
                setPortionNumber(portionNumber - 1);
                goUp()
            }}>PREV</button>}
            {pages.filter(p => p >= leftPortionPageNumber && p <= rightPortionPageNumber).map(p => {

                return <span key={p} onClick={(e) => {
                    onPageChanged(p);
                    goUp()
                }}
                             className={currentPage === p ? s.selectedPage : undefined + ' ' + s.pageNumber}>{p}</span>;
            })}
            <div> {portionCount > portionNumber
            && <button onClick={() => {
                setPortionNumber(portionNumber + 1);
                goUp()
            }}>NEXT</button>}
            </div>
        </div>

    )
};

export default Paginator;
