import React from 'react';
import s from './FormsControls.module.css';
import {Field} from "redux-form";

const FormControl = ({meta: {touched,error}, children}) => {
    const hesError = touched && error;
    return (
        <div className={s.formControl + ' ' + (hesError ? s.error : '')}>
            <div>
                {children}
            </div>
            {hesError && <span>{error}</span>}
        </div>
    )
};
export const Textarea = (props) => {
    const {input, meta, child, ...restProps} = props;
    return <FormControl {...props}><textarea {...input} {...restProps}/></FormControl>
};
export const Input = (props) => {
    const {input, meta, child, ...restProps} = props;
    return <FormControl {...props}><input {...input} {...restProps}/></FormControl>
};

export const createField = (placeholder, name, validator, component, props = {}, text = '') => (
    <div>
        <Field placeholder={placeholder} name={name} component={component} validate={validator} {...props} />{text}
    </div>
);